#hero {
  margin-top: 14em;
  width: 100%;
  padding: 0 9em;

  align-items: center;
  display: flex;
}
#hero > article[id="headers"] {
  flex-grow: 2;
  display: flex;
  flex-flow: column;
}
#hero > article[id="headers"] p {
  margin-top: 0.5em;
  font-size: 1.2em;
  line-height: 1.5;
  width: 45%;
}
#hero > article[id="headers"] h1 {
  width: 70%;
  display: flex;
  /* letter-spacing: 1px; */
  flex-flow: column;
}
#hero > article[id="headers"] h1 > span:nth-child(2) {
  color: rgb(6, 90, 130);
}

#hero > article[id="headers"] button {
  filter: drop-shadow(1px 2px 5px rgba(0, 0, 0, 0.2));
  background-color: aliceblue;
  margin-top: 1.5em;
  align-self: flex-start;
  width: fit-content;
  width: -moz-fit-content;
  display: flex;
  align-items: center;
  border-radius: 2em;
  color: rgb(27, 59, 111);
  border: none;
  gap: 0.5em;
  cursor: pointer;
  padding: 0.5em 1em;
}
#hero > article[id="headers"] button img {
  width: 2rem;
}
#hero > article[id="hero-image"] {
  flex-grow: 1;
  position: relative;

  height: 40px;
  justify-content: flex-end;
  display: flex;
}
#hero > article[id="hero-image"] img {
  position: absolute;
  transform: translateY(-12em) translateX(4em);
  width: 40em;
  filter: drop-shadow(0 10px 2px rgba(0, 0, 0, 0.4));
}
