@font-face {
  font-family: anton;
  src: url("./fonts/Anton/Anton-Regular.ttf");
}
@font-face {
  font-family: Roboto-Black;
  src: url("./fonts/roboto/Roboto-Medium.ttf");
}
@font-face {
  font-family: Roboto-Bold;
  src: url("./fonts/roboto/Roboto-Bold.ttf");
}
@font-face {
  font-family: Roboto-Regular;
  src: url("./fonts/roboto/Roboto-Regular.ttf");
}
@font-face {
  font-family: RobotoCondensed-Light;
  src: url("./fonts//Roboto_Condensed/static/RobotoCondensed-Light.ttf");
}
@font-face {
  font-family: open-sans-semi-condensed_regular;
  src: url("./fonts/Open_Sans/static/OpenSans-SemiBold.ttf");
}
@font-face {
  font-family: right;
  src: url("./fonts/Jura/static/Jura-SemiBold.ttf");
}
@font-face {
  font-family: open-sans-semi-bold;
  src: url("./fonts/Open_Sans/static/OpenSans-SemiBold.ttf");
}
@font-face {
  font-family: open-sans-regular;
  src: url("./fonts/Open_Sans/static/OpenSans-Regular.ttf");
}

header > #nav-brand {
  font-family: anton;
}
#nav-bar > ul a {
  font-family: right;
}

#hero h1 {
  font-family: Roboto-Bold;
  font-size: 2.5em;
}
#hero p {
  font-family: Roboto-Regular;
  width: 90%;
}
#hero button,
#service-list > article p {
  font-family: open-sans-regular;
}
#slide-service-list h3 {
  font-family: Roboto-Regular;
}
#service-list #title-sect > span {
  font-family: Roboto-Bold;
}
#service-list > article:nth-child(1) > p {
  font-family: RobotoCondensed-Light;
}
#service-list > article h3 {
  font-family: open-sans-semi-bold;
}
