header {
  position: absolute;
  z-index: 5000;
  left: 0;
  right: 1%;
  display: flex;
  padding: 0 9em;
  /* padding-right: 8.5em; */
  padding-top: 3em;
  align-items: center;
  justify-content: space-between;
}
div[id="nav-brand"] {
  display: flex;
  font-size: 2em;
  color: rgb(28, 114, 147);
  gap: 0.2em;
}
div[id="nav-brand"] > span:nth-child(1) {
  color: aliceblue;
}
div[id="toggle-button"] {
  cursor: pointer;
  align-items: flex-end;
  display: flex;
  gap: 0.8em;
  flex-flow: column;
}
div[id="toggle-button"] > span {
  height: 3px;
  border-radius: 100px;
  width: 3vw;
  background-color: aliceblue;
}
.sec-bar-animate {
  width: 1.5vw !important;
}
