#back-ground-image {
  display: flex;
  border: none;
  box-shadow: 5px 2px 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  z-index: -1;
  transform: rotateZ(-25deg) translateY(-17em) translateX(5em);
  height: 100%;
  background-image: linear-gradient(45deg, rgb(27, 59, 111), rgb(27, 59, 111));
  position: absolute;
  border-bottom-left-radius: 5em;
}
