#services {
  margin-top: 29vh;
  display: flex;
  flex-flow: column;
  height: 50vh;
}
#services > div[id="slide-service-list"] {
  margin-left: 9em;
  margin-right: 9em;
  display: grid;
  gap: 2em;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 45vh;
}
#services > div[id="slide-service-list"] figure {
  position: relative;
  background-color: rgb(70, 70, 70);
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.3);
  border-radius: 2em;
  overflow: hidden;
  display: flex;
  justify-content: center;
}
#services > div[id="slide-service-list"] figure img {
  position: absolute;
  width: 100%;

  height: 100%;
  object-fit: cover;
}
#services > div[id="slide-service-list"] figure figcaption {
  display: flex;
  justify-content: center;
  width: 100%;
  /* background-color: rgb(155, 178, 214); */
  padding-top: 1em;
  padding-bottom: 1em;
  align-self: flex-end;
  z-index: 2;
}
#services > div[id="slide-service-list"] figure figcaption h3 {
  color: rgb(39, 39, 39);
  display: flex;
}
#services > div[id="slide-service-list"] figure figcaption h3 > span {
  font-weight: 100;
}
#service-list {
  margin-top: 7em;
  gap: 2em;
  padding: 0 9em;
  display: grid;
  align-items: flex-start;
  grid-template-columns: repeat(3, 1fr);
}
#service-list > article:nth-child(1) > p {
  font-size: 3.5em;
  font-weight: 100;
  line-height: 1.1;
}
#service-list #title-sect > span {
  font-size: 1.3em;
  color: rgb(20, 114, 158);
}
#service-list > article p {
  line-height: 1.8;
}
#service-list > article h3 {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}
#service-list img {
  width: 3em;
}
