#nav-bar {
  position: relative;
  align-self: flex-end;
  opacity: 0;
  z-index: 6000;
  /* margin-top: 2em; */
  right: -50%;
  display: flex;
  background-color: aliceblue;
  width: fit-content;
  height: 100vh;
  width: moz-fit-content;
  justify-content: flex-end;
  transition: opacity right 0.3s linear;
}
.slide-nav {
  opacity: 1 !important;
  right: 0 !important;
}
#nav-bar > ul {
  position: relative;
  padding: 5em 1em;
  align-items: flex-end;
  list-style: none;
  background-color: rgb(27, 59, 111);
  gap: 2.5em;
  display: flex;
  justify-content: center;
  flex-flow: column;
}

#nav-bar > ul a {
  text-decoration: none;
}
#nav-bar > ul a:any-link {
  color: aliceblue;
}
